@import url(https://fonts.googleapis.com/css?family=Sarina|Montserrat:400,700,800&display=swap);
* {
  padding: 0;
  margin: 0;
  transition: color ease 0.3s;
  transition: background-color ease 0.3s; }


.framer-motion-1__box {
  width: 200px;
  height: 200px;
  background: linear-gradient(45deg, hotpink, turquoise);
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center; }
  .framer-motion-1__box h1 {
    color: white;
    font-family: 'Sarina';
    font-weight: 400;
    text-shadow: 0 0 5px rgba(0, 0, 0, 0.4); }

.framer-motion-2 .list {
  list-style: none;
  color: white; }
  .framer-motion-2 .list li {
    padding: 20px;
    width: 200px;
    margin-bottom: 10px;
    border-radius: 18px;
    font-family: 'Sarina';
    box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.3); }
    .framer-motion-2 .list li:nth-child(1) {
      background: linear-gradient(30deg, #6441A5, #2a0845); }
    .framer-motion-2 .list li:nth-child(2) {
      background: linear-gradient(30deg, #D38312, #A83279); }
    .framer-motion-2 .list li:nth-child(3) {
      background: linear-gradient(30deg, #9D50BB, #6E48AA); }
    .framer-motion-2 .list li:nth-child(4) {
      background: linear-gradient(30deg, #00c6ff, #0072ff);
      margin-bottom: 0px; }

.framer-motion-3 .list {
  list-style: none;
  color: white; }
  .framer-motion-3 .list li {
    padding: 10px 20px;
    width: 200px;
    margin-bottom: 5px;
    border-radius: 18px;
    font-family: 'Montserrat';
    box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.3);
    background: linear-gradient(45deg, hotpink, turquoise); }
    .framer-motion-3 .list li:last-child {
      margin-bottom: 0px; }

.framer-motion-4 .circle-spinner {
  width: 5rem;
  height: 5rem;
  box-sizing: border-box;
  border: solid 1rem turquoise;
  border-top: solid 1rem hotpink;
  border-radius: 120px; }

.button {
  padding: 10px 20px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  outline: none; }

.spring-react-1 .progress {
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  width: 100%;
  height: 40px; }
  .spring-react-1 .progress::-webkit-progress-bar {
    background-color: #eee;
    border-radius: 50px;
    padding: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25) inset;
    overflow: hidden; }
  .spring-react-1 .progress::-webkit-progress-value {
    background-image: -webkit-linear-gradient(-45deg, transparent 33%, rgba(0, 0, 0, 0.1) 33%, rgba(0, 0, 0, 0.1) 66%, transparent 66%), -webkit-linear-gradient(left, hotpink, turquoise);
    border-radius: 50px;
    background-size: 35px 20px, 100% 100%, 100% 100%; }

